}

::-webkit-scrollbar-thumb {
  margin: 5px 0;
  border: solid;
  border-width: 10px 0 10px 15px;
 
  border-image: url("/src/images/interface/scrollbar_test.png") 10 15;
  border-image-repeat: stretch;
}

::-webkit-scrollbar-thumb:hover {
  /* does not work. */
  /* filter: brightness(130%); */
}

textarea::-webkit-scrollbar {
  /* interestingly, this must exist for the custom scrollbar to work, and it must not be within the regular ::-webkit-scrollbar style. */
  display: block;
}

